/* #region Emblem-Card */
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
/* #endregion */

.card-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 10px;
  padding: 20px;
  margin: 0;
}

/* #region Emblem-Card */
.emblem-container {
  background-color: #61dafb;
  border: 1px solid #282c34;
  border-radius: 15px;
  width: 250px;
  padding: 8px;
  margin: 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.id {
  margin: unset;
  
}

.emblem-name {
  margin: auto;
  width: fit-content;
  color: white;
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 1px 1px 0 #000;
  user-select: none;
}

.images {
  width: 150px;
  width: 150px;
  margin: auto;
  user-select: none;
}

.emblem-icon {
  height:  200px;
  margin: 0;
  align-self: center;
}

[owned="false"] {
  filter: brightness(30%);
}

[owned="true"] {
  filter: brightness(100%);
}

.six-star {
  background-color: darkgoldenrod;
}

.five-star, .four-star {
  background-color: darkred;
}

.three-star {
  background-color: #CF770A;
}

.two-star, .one-star {
  background-color: mediumblue;
}

.emblem-rarity {
  margin-top: 8px;
  font-size: 1.2rem;
  text-align: center;
  font-weight: bold;
  color: white;
  padding: 4px 0;
  border-radius: 0 0 6px 6px;
}

.input-with-button {
  display: flex;
  align-items: center;
  width: 100%;
  position: relative;
}

.filter-input {
  flex: 1;
  padding-right: 2.5rem; /* Makes room visually for the button if needed */
  box-sizing: border-box;
}

.copy-btn {
  margin-left: -2.5rem;
  background: none;
  border: none;
  cursor: pointer;
  font-size: 1.2rem;
  padding: 0 0.5rem;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #555;
}

.copy-btn:hover {
  color: #000;
}

/* #endregion */

/* #region Filters */
/* Filters Section Styling */
.filters-container {
  display: flex;
  flex-direction: column;
  padding: 20px;
  background-color: #f8f8f8;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  margin: 20px auto;
  width: 100%;
  max-width: 600px;
  box-sizing: border-box; /* Ensure padding is included in the container's width calculation */
  /* Ensure filters stay centered even when no content */
  display: flex;
  justify-content: center;
  border: 1px solid #282c34;
}

/* Each filter group (label + input/select) */
.filter-group {
  margin-bottom: 15px;
  width: 100%;
}

/* Inline group for Set Number and Owned Status */
.filter-group-inline {
  display: flex;
  justify-content: space-between;
  gap: 15px;
  width: 100%;
}

/* Filter items inside inline group (Set and Owned dropdowns) */
.filter-item {
  flex: 1;
}

/* Label for filter inputs/select */
.filter-label {
  font-size: 1rem;
  font-weight: bold;
  color: #333;
  margin-bottom: 8px;
  display: block;
}

.filter-checkbox {
  width: 20px;
  height: 20px;
  margin-top: 8px;
  cursor: pointer;
}

/* Dropdowns for Set Number and Owned Status */
.filter-dropdown {
  width: 100%;
  padding: 10px;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 8px;
  background-color: #fff;
  transition: all 0.3s ease-in-out;
  box-sizing: border-box; /* Ensure padding doesn't affect width */
}

.filter-dropdown:hover,
.filter-dropdown:focus {
  border-color: #61dafb;
  outline: none;
}

/* Input for the Emblem Collection ID */
.filter-input {
  width: 100%;
  padding: 10px;
  font-size: 1rem;
  border: 1px solid #ccc;
  border-radius: 8px;
  background-color: #fff;
  transition: all 0.3s ease-in-out;
  box-sizing: border-box; /* Ensure padding doesn't affect width */
}

.filter-input:hover,
.filter-input:focus {
  border-color: #61dafb;
  outline: none;
}

/* Optional: Add placeholder text styling for input */
.filter-input::placeholder {
  color: #888;
  font-style: italic;
}

.filters-header {
  display: flex;
  justify-content: space-between;
}

.filter-toggle,
.progress-toggle {
  padding: 6px 12px;
  background-color: #3a3a3a;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  justify-content: center;
}

.filter-toggle:hover,
.progress-toggle:hover {
  background-color: #505050;
}

.collection-progress {
  background-color: #f9f9f9;
  padding: 15px;
  border-radius: 8px;
  margin: 20px 0;
  border: 1px solid #ddd;
}

.progress-filter {
  margin-bottom: 10px;
}

.progress-filter select {
  margin-left: 10px;
  padding: 5px;
}

.rarity-progress .rarity-row {
  padding: 4px 0;
}

#filter-section  {
  margin-top: 10px;
}

.rarity-progress {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.rarity-row {
  display: flex;
  align-items: center;
  gap: 12px;
}

.rarity-label {
  width: 50px;
  text-align: right;
}

.progress-bar-container {
  flex: 1;
  height: 20px;
  background-color: #ddd;
  border-radius: 4px;
  overflow: hidden;
}

.progress-bar {
  height: 100%;
  border-radius: 4px 0 0 4px;
}

.progress-text {
  width: 120px;
  text-align: right;
  font-size: 0.9em;
}

.overall-bar {
  background-color: seagreen;
}

.overall-progress {
  margin-top: 10px;
  border-top: 1px solid #ccc;
  padding-top: 10px;
}
/* #endregion Filters */